*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body{
  height: 100vh;
  background: linear-gradient(90deg, rgba(255, 157, 211, 0.8), rgba(190, 162, 255, 0.2) 90%),
            linear-gradient(150deg, rgba(50, 255, 255, 0.8), rgb(166, 95, 248) 90%);
  color: rgb(144, 0, 144);
  }
#root{
  height: 100%;
}
.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
#first-row{
  display: flex;
  justify-content: center;
}
.setting{
  margin: 10px;
  padding: 20px;
  border: solid 5px rgb(129, 129, 235);
  border-radius: 20px;
}
h1{
  margin: 5px 0 25px 0;
  font-family: 'Raleway', sans-serif;
}
.scale{
  margin-top: 10px;
  display: flex;
  justify-content: center;
  font-size: 35px;
}
.scale p {
  margin: 0 10px 0 10px;
  font-weight: bold;
}
#time{
  margin: 10px;
  padding: 30px 50px 30px 50px;
  border: solid 5px rgb(100, 100, 170);
  border-radius: 40px;
  text-align: center;
}
#time #time-left {
  font-size: 70px;
font-weight: bold;
}
#fa-container{
  display: flex;
  justify-content: space-evenly;
  
}
#start_stop{
  font-size: 40px;
  position: relative;
  height: 41px;
  width: 41px;
  background-color: rgba(0,0,0,0);
  border-radius: 50%;
  cursor: pointer;
  border:rgba(0,0,0,0);
  color: rgb(144, 0, 144);
}
.start-stop-icon{
  position: absolute;
  top: 0;
  left: 0;
}
#stop-icon{
  display: none;
}
#reset{
  height: 41px;
  width: 41px;
  font-size: 40px;
  background-color: rgba(0,0,0,0);
  border-radius: 50%;
  cursor: pointer;
  border:rgba(0,0,0,0);
  color: rgb(144, 0, 144);
}
